// Import react dependecies
import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useState, useRef } from "react";

// Import classes
import classes from "./AddUser.module.css";

// import helpers
import {
  slugyfy,
  generateID,
  selectRandomImageFromSet,
} from "../../Helpers/helpers";

const AddUsers = (props) => {
  // add useselector hook for data
  const users = useSelector((state) => state.users);
  const userImagesSet = useSelector(
    (state) => state.settings.userImagesSamples
  );

  // Add useState and useRef hook to validate form
  const [formIsValid, setFormIsValid] = useState(true);
  const [errorString, setErrorString] = useState("");
  const userNameInput = useRef("");
  const userTeamInput = useRef("");
  const formRef = useRef();

  // add distaptch
  const dispatch = useDispatch();

  // Handle inputs
  const onHandleFormInputs = () => {
    setFormIsValid(true);
  };

  // add user Handler
  const addUserHandler = (name, team) => {
    const userImage = selectRandomImageFromSet(userImagesSet, 0, 0);
    dispatch({
      type: "add_user",
      newUser: {
        id: generateID(),
        imageName: userImage.name,
        fullName: name,
        team: team,
      },
      userKey: slugyfy(name),
      imageId: userImage.id,
    });
    // clear fields
    formRef.current.reset();
  };

  // Add user to list
  const submitUserHandler = (event) => {
    event.preventDefault();
    const userNameInputValue = userNameInput.current.value;
    const userTeamInputValue = userTeamInput.current.value;

    // Validate data
    if (!userNameInputValue || !userTeamInputValue) {
      setFormIsValid(false);
      setErrorString(
        "Stai cercando di creare un fantallenatore senza aver inserito adeguatamente i dati. Inserisci tutti i dati e riprova."
      );
      return;
    } else if (slugyfy(userNameInputValue) in users) {
      setFormIsValid(false);
      setErrorString("L\'username che stai cercando di creare esiste già");
      return;
    }
    for (var key in users) {
      if (users[key].team === userTeamInputValue) {
        setFormIsValid(false);
        setErrorString("La squadra che stai cercando di creare esiste già");
        return;
      }
    }

    // Add element
    addUserHandler(userNameInputValue, userTeamInputValue);
  };

  // Handle listener on change user data
  const onChangeDataHandler = (key, field, input) => {
    const value = input.target.value;
    if (value !== "") {
      dispatch({
        type: "update_user",
        field: field,
        key: key,
        newValue: value,
      });
    }
  };

  // Handle delete user
  const onDeleteUser = (key, team, event) => {
    if (
      window.confirm(
        `Sei sicuro di voler cancellare ${team}? Questa operazione è irreversibile.`
      )
    ) {
      dispatch({
        type: "delete_user",
        key: key,
      });
    }
  };

  return (
    <div className={classes.wrapper}>
      <div className={classes.usersList} style={{marginTop: '0px'}}>
        <h3 className={classes.subitle}>Lista fantallenatori</h3>
        {Object.keys(users).length === 0 && <p>Non hai ancora creato nessun fantallenatore.</p>}
        {Object.keys(users).map(function (keyName, keyIndex) {
          const src = require("../../Assets/userProfile/" +
            users[keyName].imageName);
          return (
            <div key={users[keyName].id} className={classes.userBlock}>
              <div style={{ position: "absolute" }}>
                <div
                  className={classes.deleteElement}
                  onClick={onDeleteUser.bind(
                    this,
                    keyName,
                    users[keyName].team
                  )}
                >
                  <svg width="20" height="20" x="0" y="0" viewBox="0 0 512 512">
                    <g>
                      <g xmlns="http://www.w3.org/2000/svg">
                        <path
                          d="m424 64h-88v-16c0-26.467-21.533-48-48-48h-64c-26.467 0-48 21.533-48 48v16h-88c-22.056 0-40 17.944-40 40v56c0 8.836 7.164 16 16 16h8.744l13.823 290.283c1.221 25.636 22.281 45.717 47.945 45.717h242.976c25.665 0 46.725-20.081 47.945-45.717l13.823-290.283h8.744c8.836 0 16-7.164 16-16v-56c0-22.056-17.944-40-40-40zm-216-16c0-8.822 7.178-16 16-16h64c8.822 0 16 7.178 16 16v16h-96zm-128 56c0-4.411 3.589-8 8-8h336c4.411 0 8 3.589 8 8v40c-4.931 0-331.567 0-352 0zm313.469 360.761c-.407 8.545-7.427 15.239-15.981 15.239h-242.976c-8.555 0-15.575-6.694-15.981-15.239l-13.751-288.761h302.44z"
                          fill="#fff"
                          data-original="#000000"
                        />
                        <path
                          d="m256 448c8.836 0 16-7.164 16-16v-208c0-8.836-7.164-16-16-16s-16 7.164-16 16v208c0 8.836 7.163 16 16 16z"
                          fill="#fff"
                          data-original="#000000"
                        />
                        <path
                          d="m336 448c8.836 0 16-7.164 16-16v-208c0-8.836-7.164-16-16-16s-16 7.164-16 16v208c0 8.836 7.163 16 16 16z"
                          fill="#fff"
                          data-original="#000000"
                        />
                        <path
                          d="m176 448c8.836 0 16-7.164 16-16v-208c0-8.836-7.164-16-16-16s-16 7.164-16 16v208c0 8.836 7.163 16 16 16z"
                          fill="#fff"
                          data-original="#000000"
                        />
                      </g>
                    </g>
                  </svg>
                </div>
              </div>
              <div
                className={classes.userImg}
                style={{ backgroundImage: `url("${src.default}")` }}
                alt={users[keyName].team}
                title={users[keyName].team}
              ></div>
              <div className={classes.userData}>
                <div className={classes.userData}>
                  <label
                    htmlFor={users[keyName].fullName}
                    className={classes.inp}
                  >
                    <input
                      type="text"
                      id={users[keyName].fullName}
                      placeholder="&nbsp;"
                      defaultValue={users[keyName].fullName}
                      onChange={onChangeDataHandler.bind(
                        this,
                        keyName,
                        "fullName"
                      )}
                    />
                    <span className={classes.label}>Username</span>
                    <span className={classes["focus-bg"]}></span>
                  </label>
                </div>
                <div className={classes.userData}>
                  <label htmlFor={users[keyName].team} className={classes.inp}>
                    <input
                      type="text"
                      id={users[keyName].team}
                      placeholder="&nbsp;"
                      defaultValue={users[keyName].team}
                      onChange={onChangeDataHandler.bind(this, keyName, "team")}
                    />
                    <span className={classes.label}>Squadra</span>
                    <span className={classes["focus-bg"]}></span>
                  </label>
                </div>
              </div>
            </div>
          );
        })}
      </div>
      <div className={classes.addUser}>
        <h3 className={classes.subitle}>Crea fantallenatore</h3>
        <form
          className={classes.form}
          onSubmit={submitUserHandler.bind(this)}
          ref={formRef}
        >
          <div className={classes.userData}>
            <label htmlFor="name" className={classes.inp}>
              <input
                type="text"
                id="name"
                name="name"
                ref={userNameInput}
                onKeyDown={onHandleFormInputs}
                placeholder="&nbsp;"
              />
              <span className={classes.label}>Username</span>
              <span className={classes["focus-bg"]}></span>
            </label>
          </div>
          <div className={classes.userData}>
            <label htmlFor="team" className={classes.inp}>
              <input
                type="text"
                id="team"
                name="team"
                ref={userTeamInput}
                onKeyDown={onHandleFormInputs}
                placeholder="&nbsp;"
              />
              <span className={classes.label}>Nome squadra</span>
              <span className={classes["focus-bg"]}></span>
            </label>
          </div>
          {!formIsValid && <p className={classes.error}>{errorString}</p>}
          <div className={classes.actions}>
            <button className="primaryButton" type="submit">
              <span style={{ marginRight: 4 }}>+</span> Crea
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default AddUsers;
