// Import react dependecies
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Bar, Doughnut } from "react-chartjs-2";

import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";

// Import classes
import classes from "./UserDetail.module.css";

// import helpers
import { getColorTeam, getMaxOfferFromUser } from "../../Helpers/helpers";

//Import images
import playerLeft from "../../Assets/icons/add-football-player.svg";
import maxOffer from "../../Assets/icons/donate.svg";
import moneySpent from "../../Assets/icons/flying-money.svg";
import playerPurchased from "../../Assets/icons/football-player.svg";
import amount from "../../Assets/icons/money.svg";

const UserDetail = (props) => {
  const players = useSelector((state) => state.players);
  const users = useSelector((state) => state.users);
  const settings = useSelector((state) => state.settings);

  // Set order
  const order = ["P", "D", "C", "A"];

  // set user
  const [user, setUser] = useState(users[props.userKey]);
  const [userKey, setUserKey] = useState(props.userKey);

  // select the players owned by this user
  let ownedPlayers = [];
  let teamsofOwnedPlayers = [];
  let rolesOfOwnedRoles = [
    { role: "Por", count: 0 },
    { role: "Dc", count: 0 },
    { role: "Dd", count: 0 },
    { role: "Ds", count: 0 },
    { role: "E", count: 0 },
    { role: "M", count: 0 },
    { role: "C", count: 0 },
    { role: "W", count: 0 },
    { role: "T", count: 0 },
    { role: "A", count: 0 },
    { role: "Pc", count: 0 },
  ];
  let amountSpent = 0;
  let amountSpentForP = 0;
  let amountSpentForD = 0;
  let amountSpentForC = 0;
  let amountSpentForA = 0;
  Object.keys(players).forEach(function (key) {
    if (players[key].purchased_by === userKey) {
      // Get owned players and their total cost
      ownedPlayers.push(players[key]);
      amountSpent = amountSpent + parseInt(players[key].purchased_for);
      switch (players[key].macrorole) {
        case "P":
          amountSpentForP = amountSpentForP + players[key].purchased_for;
          break;
        case "D":
          amountSpentForD = amountSpentForD + players[key].purchased_for;
          break;
        case "C":
          amountSpentForC = amountSpentForC + players[key].purchased_for;
          break;
        case "A":
          amountSpentForA = amountSpentForA + players[key].purchased_for;
          break;
      }

      // Fet teams of owned players
      let existsTeam = teamsofOwnedPlayers.find(
        (o) => o.team === players[key].team
      );

      if (existsTeam) {
        const index = teamsofOwnedPlayers
          .map((el) => el.team)
          .indexOf(players[key].team);
        teamsofOwnedPlayers[index].count += 1;
      } else {
        teamsofOwnedPlayers.push({ team: players[key].team, count: 1 });
      }

      // Get roles of owned players
      const mantraRolesAry = players[key].role.split(";");
      mantraRolesAry.forEach((role) => {
        const index = rolesOfOwnedRoles.map((el) => el.role).indexOf(role);
        rolesOfOwnedRoles[index].count += 1;
      });
    }
  });

  // sort array
  if (ownedPlayers.length > 1) {
    ownedPlayers.sort(function (a, b) {
      const indexA = order.indexOf(a.macrorole);
      const indexB = order.indexOf(b.macrorole);
      const diff = indexA - indexB;
      const diff2 = a.shuffled_times - b.shuffled_times;
      return diff || diff2;
    });
  }

  // Set data for percentae chart
  const percentageChartData = {
    labels: ["Portieri", "Difensori", "Centrocampisti", "Attaccanti"],
    datasets: [
      {
        label: "Percentuale su " + settings.user_mln + " mln",
        data: [
          Math.round(
            (parseInt(amountSpentForP) / parseInt(settings.user_mln)) * 100 * 10
          ) / 10,
          Math.round(
            (parseInt(amountSpentForD) / parseInt(settings.user_mln)) * 100 * 10
          ) / 10,
          Math.round(
            (parseInt(amountSpentForC) / parseInt(settings.user_mln)) * 100 * 10
          ) / 10,
          Math.round(
            (parseInt(amountSpentForA) / parseInt(settings.user_mln)) * 100 * 10
          ) / 10,
        ],
        backgroundColor: [
          "rgba(255, 206, 86, 0.2)",
          "rgba(75, 192, 192, 0.2)",
          "rgba(54, 162, 235, 0.2)",
          "rgba(255, 99, 132, 0.2)",
        ],
        borderColor: [
          "rgba(255, 206, 86, 1)",
          "rgba(75, 192, 192, 1)",
          "rgba(54, 162, 235, 1)",
          "rgba(255, 99, 132, 1)",
        ],
        borderWidth: 1,
      },
    ],
  };
  const percentageChartOptions = {
    indexAxis: "y",
    // Elements options apply to all of the options unless overridden in a dataset
    // In this case, we are setting the border of each horizontal bar to be 2px wide
    elements: {
      bar: {
        borderWidth: 2,
      },
    },
    responsive: true,
    plugins: {
      legend: {
        position: "right",
        display: false,
      },
      title: {
        display: true,
        text: "Spese ripartite (in %)",
      },
    },
    maintainAspectRatio: false,
  };

  // Set data for team pie chart
  const teamChartData = {
    labels: teamsofOwnedPlayers.map((element, index) => element.team),
    datasets: [
      {
        label: "Distribuzione squadre",
        backgroundColor: teamsofOwnedPlayers.map((element, index) =>
          getColorTeam(element.team)
        ),
        data: teamsofOwnedPlayers.map((element, index) => element.count),
      },
    ],
  };

  // Set data for roles bar chart
  const rolesChartData = {
    labels: rolesOfOwnedRoles.map((element, index) => element.role),
    datasets: [
      {
        label: "Calciatori:",
        data: rolesOfOwnedRoles.map((element, index) => element.count),
        backgroundColor: [
          "#FFFB00",
          "#00D603",
          "#00D603",
          "#00D603",
          "#00BEFF",
          "#00BEFF",
          "#00BEFF",
          "#AC2EFA",
          "#AC2EFA",
          "#D20202",
          "#D20202",
        ],
        // borderColor: [
        //   "rgba(255, 206, 86, 1)",
        //   "rgba(75, 192, 192, 1)",
        //   "rgba(54, 162, 235, 1)",
        //   "rgba(255, 99, 132, 1)",
        // ],
        // borderWidth: 1,
      },
    ],
  };
  const rolesChartOptions = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
            precision: 0,
            fixedStepSize: 1,
          },
        },
      ],
    },
    maintainAspectRatio: false,
  };

  // Handle shift user
  const shiftUser = (direction) => {
    const keys = Object.keys(users);
    const namesArray = [];
    keys.forEach((element, index) => {
      namesArray.push(users[element].fullName);
    });
    var currentIndex = namesArray.indexOf(user.fullName);
    let nextIndex = 0;
    if (direction === "right") {
      if (currentIndex + 1 > namesArray.length - 1) {
        nextIndex = 0;
      } else {
        nextIndex = currentIndex + 1;
      }
    } else if (direction === "left") {
      if (currentIndex === 0) {
        nextIndex = namesArray.length - 1;
      } else {
        nextIndex = currentIndex - 1;
      }
    }
    // shift user
    const nextName = namesArray[nextIndex];
    keys.forEach((element, index) => {
      if (users[element].fullName === nextName) {
        setUser(users[element]);
        setUserKey(element);
      }
    });
  };

  // Define tabpanels
  function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
      <div
        // style={{ width: "100%" }}
        role="tabpanel"
        hidden={value !== index}
        id={`vertical-tabpanel-${index}`}
        aria-labelledby={`vertical-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box p={3}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }

  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
  };

  function a11yProps(index) {
    return {
      id: `vertical-tab-${index}`,
      "aria-controls": `vertical-tabpanel-${index}`,
    };
  }

  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      backgroundColor: theme.palette.background.paper,
      display: "flex",
      minHeight: "280px",
    },
    tabs: {
      borderRight: `1px solid ${theme.palette.divider}`,
    },
  }));

  const tabClasses = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
      <h2 style={{ marginTop: "0" }}>Dati di {user.team}</h2>
      <div className={tabClasses.root}>
        <Tabs
          orientation="vertical"
          variant="scrollable"
          value={value}
          onChange={handleChange}
          aria-label="Vertical tabs example"
          className={tabClasses.tabs}
        >
          <Tab label="Rosa" {...a11yProps(0)} />
          <Tab label="Analisi" {...a11yProps(1)} />
        </Tabs>
        <TabPanel value={value} index={0}>
          <div className={classes.tableContainer}>
            {ownedPlayers.length > 0 && (
              <table>
                <thead>
                  <tr>
                    <th scope="col" style={{ textAlign: "left" }}>
                      Nome
                    </th>
                    <th scope="col" style={{ textAlign: "left" }}>
                      Squadra
                    </th>
                    <th scope="col" style={{ textAlign: "left" }}>
                      Ruolo
                    </th>
                    <th scope="col">Costo</th>
                  </tr>
                </thead>
                <tbody>
                  {ownedPlayers.map((element) => {
                    let namesArray = element.name.split(" ");
                    namesArray.forEach((element, index) => {
                      namesArray[index] =
                        namesArray[index].charAt(0) +
                        namesArray[index].slice(1).toLowerCase();
                    });
                    const stringName = namesArray.join(" ");
                    const rolesAry = element.role.split(";");
                    const src = require("../../Assets/stemmi/" +
                      element.team +
                      ".png");
                    return (
                      <tr key={element.id}>
                        <td>{stringName}</td>
                        <td>
                          <span
                            style={{
                              display: "flex",
                              alignItems: "center",
                            }}
                          >
                            <img src={src.default} className="smallTeamLogo" />
                            {element.team}
                          </span>
                        </td>
                        <td>
                          <span style={{ display: "flex" }}>
                            {settings.game_type === "mantra" ? (
                              rolesAry.map((role) => {
                                return (
                                  <span
                                    key={element.id + role.toLowerCase()}
                                    className={
                                      "roleBadge " + role.toLowerCase()
                                    }
                                  >
                                    {role}
                                  </span>
                                );
                              })
                            ) : (
                              <span
                                className={
                                  "roleBadge " + element.macrorole.toLowerCase()
                                }
                              >
                                {element.macrorole}
                              </span>
                            )}
                          </span>
                        </td>
                        <td style={{ textAlign: "center" }}>
                          {element.purchased_for}
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            )}
            {!ownedPlayers.length && (
              <p style={{textAlign: 'center'}}>Non hai ancora acquistato nessun giocatore.</p>
            )}
          </div>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <div className={classes.flexWrapper}>
            <div className={classes.card}>
              <h3 style={{ marginTop: "0px" }}>Informazioni principali</h3>
              <ul className={classes.list}>
                <li>
                  <img src={moneySpent} />
                  Milioni spesi:
                  <span className={classes.listItem}>{amountSpent}</span>
                </li>
                <li>
                  <img src={amount} />
                  Milioni rimasti:
                  <span className={classes.listItem}>
                    {settings.user_mln - amountSpent}
                  </span>
                </li>
                <li>
                  <img src={playerPurchased} />
                  Calciatori acquistati:
                  <span className={classes.listItem}>
                    {ownedPlayers.length}
                  </span>
                </li>
                <li>
                  <img src={playerLeft} />
                  Calciatori (min) da acquistare:
                  <span className={classes.listItem}>
                    {settings.min_players - ownedPlayers.length}
                  </span>
                </li>
                <li>
                  <img src={maxOffer} />
                  Massima offerta:
                  <span className={classes.listItem}>
                    {getMaxOfferFromUser(
                      userKey,
                      players,
                      settings.user_mln,
                      settings.min_players
                    )}
                  </span>
                </li>
              </ul>
            </div>
            <div className={`${classes.card} ${classes.w50}`}>
              <Bar
                data={percentageChartData}
                options={percentageChartOptions}
              />
            </div>
          </div>
          <div className={classes.flexWrapper}>
            <div className={classes.card}>
              <p style={{ margin: "0", textAlign: "center" }}>
                Distribuzione squadre
              </p>
              <Doughnut
                data={teamChartData}
                options={{
                  legend: { display: true, position: "right" },

                  datalabels: {
                    display: true,
                    color: "white",
                  },
                  tooltips: {
                    backgroundColor: "#5a6e7f",
                  },
                }}
              />
            </div>
            <div className={`${classes.card} ${classes.w50}`}>
              <Bar data={rolesChartData} options={rolesChartOptions} />
            </div>
          </div>
        </TabPanel>
      </div>
      <span
        className={classes.leftskipper}
        onClick={() => {
          shiftUser("left");
        }}
      >
        <svg
          enableBackground="new 0 0 512 512"
          version="1.1"
          width="30"
          height="30"
          viewBox="0 0 512 512"
          style={{ transform: "scale(1, -1)" }}
        >
          <linearGradient
            id="a"
            x1="126.51"
            x2="385.49"
            y1="258"
            y2="258"
            gradientTransform="matrix(1 0 0 -1 0 514)"
            gradientUnits="userSpaceOnUse"
            xmlns="http://www.w3.org/2000/svg"
          >
            <stop stopColor="#42445a" offset="0" />
            <stop stopColor="#42445a" offset=".021" />
            <stop stopColor="#42445a" offset=".293" />
            <stop stopColor="#42445a" offset=".554" />
            <stop stopColor="#42445a" offset=".796" />
            <stop stopColor="#1a1a1a" offset="1" />
          </linearGradient>
          <path d="m146.51 512c-5.111 0-10.224-1.947-14.127-5.843-7.819-7.802-7.832-20.465-0.03-28.284l207.28-207.72c7.813-7.813 7.813-20.501 0.015-28.299l-207.3-207.73c-7.802-7.819-7.789-20.482 0.03-28.284 7.817-7.802 20.48-7.79 28.284 0.029l207.28 207.72c23.379 23.379 23.379 61.444-0.015 84.838l-207.27 207.7c-3.907 3.915-9.032 5.873-14.157 5.873z" />
        </svg>
      </span>
      <span
        className={classes.rightskipper}
        onClick={() => {
          shiftUser("right");
        }}
      >
        <svg
          enableBackground="new 0 0 512 512"
          version="1.1"
          width="30"
          height="30"
          viewBox="0 0 512 512"
        >
          <linearGradient
            id="a"
            x1="126.51"
            x2="385.49"
            y1="258"
            y2="258"
            gradientTransform="matrix(1 0 0 -1 0 514)"
            gradientUnits="userSpaceOnUse"
            xmlns="http://www.w3.org/2000/svg"
          >
            <stop stopColor="#42445a" offset="0" />
            <stop stopColor="#42445a" offset=".021" />
            <stop stopColor="#42445a" offset=".293" />
            <stop stopColor="#42445a" offset=".554" />
            <stop stopColor="#42445a" offset=".796" />
            <stop stopColor="#1a1a1a" offset="1" />
          </linearGradient>
          <path d="m146.51 512c-5.111 0-10.224-1.947-14.127-5.843-7.819-7.802-7.832-20.465-0.03-28.284l207.28-207.72c7.813-7.813 7.813-20.501 0.015-28.299l-207.3-207.73c-7.802-7.819-7.789-20.482 0.03-28.284 7.817-7.802 20.48-7.79 28.284 0.029l207.28 207.72c23.379 23.379 23.379 61.444-0.015 84.838l-207.27 207.7c-3.907 3.915-9.032 5.873-14.157 5.873z" />
        </svg>
      </span>
    </>
  );
};

export default UserDetail;
