import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import Header from "./Components/Layout/Header";
import PlayerList from "./Components/Layout/PlayerList";
import PlayerTable from "./Components/Layout/PlayerTable";
import SidebarMenu from "./Components/Layout/SidebarMenu";
import Modal from "./Components/UI/Modal";
import Loader from "./Components/UI/Loader";
import Login from "./Components/Layout/Login";
import Footer from "./Components/Layout/Footer";

// Import img
import reloadButtonImage from "./Assets/icons/reload.svg";

function App() {
  // Handle menu state
  const [menuIsShown, setMenuIsShown] = useState(false);
  // Handle Modal state
  const [modalData, setModalData] = useState({
    isShown: false,
  });

  // Handle loader state
  const [loaderIsShown, setLoaderIsShown] = useState(true);

  // Handle user login
  const [userStatus, setUserStatus] = useState(
    JSON.parse(window.localStorage.getItem("fantauser"))
  );
  const [userIsLoggedIn, setUserIsLoggedIn] = useState(
    userStatus ? userStatus.isLogged : null
  );

  // Get the settings from the store
  const settings = useSelector((state) => state);
  // add distaptch
  const dispatch = useDispatch();

  // dispatch({
  //   type: "push_new_player",
  // });

  // retreive informations about settings from firebase
  const pullFromServer = () => {
    fetch(
      "https://fantaasta-37ac8-default-rtdb.europe-west1.firebasedatabase.app/settings/-McA5xpKzx99xHhBWC0W.json"
    )
      .then((response) => response.json())
      .then((data) => {
        // Dispatch action to set the current settings
        dispatch({
          type: "pull_settings",
          data: data,
        });
        setLoaderIsShown(false);
        //@todo and handle failure
      });
  };

  // Handle login
  const login = (role) => {
    setLoaderIsShown(true);
    setTimeout(function () {
      const fantauser = {
        isLogged: true,
        role: role,
      };
      window.localStorage.setItem("fantauser", JSON.stringify(fantauser));
      setUserStatus(fantauser);
      setUserIsLoggedIn(true);
      setLoaderIsShown(false);
    }, 2000);
  };

  const logout = () => {
    setLoaderIsShown(true);
    setMenuIsShown(false);
    setTimeout(function () {
      window.localStorage.removeItem("fantauser");
      setUserStatus({});
      setUserIsLoggedIn(false);
      setLoaderIsShown(false);
    }, 2000);
  };

  // Set menu handlers
  const showMenuHandler = () => {
    setMenuIsShown(true);
  };
  const hideMenuHandler = () => {
    setMenuIsShown(false);
  };

  // Set Modal handler
  const updateModalData = (data) => {
    window.scrollTo({ top: 0, behavior: "smooth" });
    setMenuIsShown(false);
    setModalData({ ...data });
  };

  const reload = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
    setLoaderIsShown(true);
    setTimeout(() => {
      pullFromServer();
    }, 1500);
  };

  // Pull from server when needed
  useEffect(() => {
    pullFromServer();
  }, []);

  return (
    <>
      {loaderIsShown && <Loader></Loader>}
      {!userIsLoggedIn && <Login makeLogin={login}></Login>}
      {userIsLoggedIn && (
        <>
          {!loaderIsShown && modalData.isShown && (
            <Modal
              content={modalData}
              onClose={updateModalData}
              onSelectOption={updateModalData}
            />
          )}
          {menuIsShown && (
            <SidebarMenu
              onCloseMenu={hideMenuHandler}
              onSelectOption={updateModalData}
              onMakeLogout={logout}
            ></SidebarMenu>
          )}
          <Header onShowMenu={showMenuHandler} />
          <main>
            <PlayerList onSelectOption={updateModalData}></PlayerList>
            <PlayerTable
              onCloseMenu={hideMenuHandler}
              onSelectOption={updateModalData}
            ></PlayerTable>
            <button className="mobileReloader" title='Scarica gli ultimi aggiornamenti' onClick={reload}>
              <img src={reloadButtonImage} alt="Pull from server" />
            </button>
          </main>
          <Footer></Footer>
        </>
      )}
    </>
  );
}

export default App;

