// Import react dependecies
import React, { useState, useRef } from "react";
import { useSelector } from "react-redux";

import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";

// Import classes
import classes from "./PersonalArea.module.css";

const PersonalArea = (props) => {
  // Get èplayers
  const players = useSelector((state) => (state ? state.players : ""));

  // UseRef Hook
  const [seletcedPlayer, setSelectedPlayer] = useState();
  const [errorString, setErrorString] = useState();
  const selectedMaxPrice = useRef();
  const autoCompleteRef = useRef();

  // Get favourtiteList
  let [favoutitesList, setavoutitesList] = useState(localStorage.getItem("favoutitesList") ? [...JSON.parse(localStorage.getItem("favoutitesList"))] : []);

  const getPlayersSubsetForFilter = (players) => {
    if (Object.keys(players).length) {
      const playersSubset = [];
      Object.keys(players).forEach(function (key) {
        playersSubset.push(players[key]);
      });
      return playersSubset;
    }
  };

  // Add player to favourite list
  const addPlayer = (e) => {
    e.preventDefault();
    if (
      !seletcedPlayer ||
      !selectedMaxPrice.current.value ||
      selectedMaxPrice.current.value < 1
    ) {
      setErrorString("Dati errati. Modifica i campi e riprova.");
      return;
    }
    // Check if present in the array
    if (favoutitesList && favoutitesList.length) {
      const exist = favoutitesList.filter(
        (element) => parseInt(element.id) === parseInt(seletcedPlayer)
      );

      if (exist.length) {
        setErrorString("Il giocatore è già stato aggiunto alla lista.");
        return;
      } else {
        setavoutitesList([
          ...favoutitesList,
          {
            id: seletcedPlayer,
            maxPrice: selectedMaxPrice.current.value,
          },
        ]);
        localStorage.setItem("favoutitesList", JSON.stringify(favoutitesList));
      }
    } else {
      setavoutitesList([
        {
          id: seletcedPlayer,
          maxPrice: selectedMaxPrice.current.value,
        },
      ]);
      localStorage.setItem("favoutitesList", JSON.stringify(favoutitesList));
    }

    // Clear form
    selectedMaxPrice.current.value = "";
  };

  const clearErrorString = (value) => {
    setSelectedPlayer(value ? value.id : null);
    setErrorString("");
  };

  const removeItem = () => {

  }

  return (
    <div>
      <h2>Area personale</h2>
      <div>
        <div>
          <h4>Aggiungi giocatore:</h4>
        </div>
        <form className={classes.flexWrapper} onSubmit={addPlayer}>
          <Autocomplete
            ref={autoCompleteRef}
            onChange={(event, value) => {
              clearErrorString(value);
            }}
            // id="combo-box-demo"
            options={[...getPlayersSubsetForFilter(players)]}
            getOptionLabel={(option) => option.name}
            style={{ width: "100%", marginRight: "20px" }}
            renderOption={(option) => (
              <div style={{ display: "flex", alignItems: "center" }}>
                <span style={{ marginRight: "8px" }}>
                  <img
                    src={
                      require("../../Assets/stemmi/" + option.team + ".png")
                        .default
                    }
                    alt=""
                    width="14px"
                  />
                </span>
                <span style={{ marginRight: "8px" }}>
                  {option.name.charAt(0) + option.name.slice(1).toLowerCase()}
                </span>
                <span className="flex">
                  {option.role.split(";").map((element) => {
                    return (
                      <span
                        key={option.id + element.toLowerCase()}
                        className={"roleBadge " + element.toLowerCase()}
                        style={{
                          fontSize: "12px",
                          width: "22px",
                          height: "22px",
                        }}
                      >
                        {element}
                      </span>
                    );
                  })}
                </span>
              </div>
            )}
            renderInput={(params) => (
              <TextField
                {...params}
                label="Cerca un giocatore"
                variant="outlined"
              />
            )}
          />
          <div className={classes.fieldWrapper}>
            <label htmlFor="max_price" className={classes.inp}>
              <input
                type="number"
                step="1"
                min="1"
                id="max_price"
                name="max_price"
                placeholder="&nbsp;"
                ref={selectedMaxPrice}
              />
              <span className={classes.label}>Prezzo massimo</span>
              <span className={classes["focus-bg"]}></span>
            </label>
          </div>
          <button type="submit" className="primaryButton">
            Aggiungi
          </button>
        </form>
        {errorString && (
          <p className="error" style={{ marginTop: "10px" }}>
            {errorString}
          </p>
        )}
        <h4 style={{ marginTop: "40px" }}>Lista:</h4>
        <ul className={classes.favouritePlayerWrapper}>
          {favoutitesList && favoutitesList.length && Object.keys(players).map((key) => {
            const currentFavPlayer = favoutitesList.filter(function (e) {
              return e.id === players[key].id;
            });

            return (
              currentFavPlayer.length > 0 && (
                <li
                  key={players[key].id}
                  className={`${classes.favouritePlayer} ${
                    players[key].purchased_by !== "" ? classes.purchased : ""
                  }`}
                  style={{ display: "flex", justifyContent: "space-between" }}
                >
                  <div className={classes.flexWrapper}>
                    <span
                      style={{
                        marginRight: "20px",
                        fontWeight: "600",
                        fontSize: "18px",
                      }}
                    >
                      {players[key].name.charAt(0) +
                        players[key].name.slice(1).toLowerCase()}
                    </span>
                    <span>
                      <img
                        style={{ marginRight: "5px" }}
                        src={
                          require("../../Assets/stemmi/" +
                            players[key].team +
                            ".png").default
                        }
                        alt=""
                        width="18"
                      />
                    </span>
                    <span
                      style={{
                        marginRight: "20px",
                      }}
                    >
                      {players[key].team}
                    </span>
                    <span className="flex" style={{ marginRight: "20px" }}>
                      {players[key].role.split(";").map((element) => {
                        return (
                          <span
                            key={players[key].id + element.toLowerCase()}
                            className={"roleBadge " + element.toLowerCase()}
                          >
                            {element}
                          </span>
                        );
                      })}
                    </span>
                    <span style={{ marginRight: "20px" }}>
                      VDM: {players[key].marketvalue}
                    </span>
                    <span style={{ marginRight: "20px" }}>
                      Massima offerta: {currentFavPlayer[0].maxPrice}
                    </span>
                  </div>
                  <div className={classes.removeItem} onClick={()=>{removeItem(players[key].id)}}>x</div>
                </li>
              )
            );
          })}
          {!favoutitesList && <p>Non hai ancora aggiunto nessun calciatore.</p>}
        </ul>
      </div>
    </div>
  );
};

export default PersonalArea;
