// Import react dependecies
import React, {Fragment} from "react";
import { useSelector } from "react-redux";

// Import classes
import classes from "./PlayerList.module.css";

// Import helpers
import {
  getAmountLeftForPlayer,
  getAmountOfPlayerLeftToBuyForUser,
} from "../../Helpers/helpers";

const PlayerList = (props) => {
  const users = useSelector((state) => state.users);
  const players = useSelector((state) => state.players);
  const settings = useSelector((state) => state.settings);
  return (
    <div className={classes.wrapper}>
      <div className={classes.container}>
        <div className={classes.sectionTitle}>
          <h2>Lista fantallenatori</h2>
        </div>

        <div className={classes.cards}>
          {Object.keys(users).map(function (keyName, keyIndex) {
            const src = require("../../Assets/userProfile/" +
              users[keyName].imageName);
            return (
              <div key={users[keyName].team}>
                <div
                  className={classes.card}
                  onClick={() => {
                    props.onSelectOption({
                      action: "userDetail",
                      isShown: true,
                      userKey: keyName,
                    });
                  }}
                >
                  <img
                    src={src.default}
                    className={classes["card__image"]}
                    alt={users[keyName].team}
                  />
                  <div className={classes["card__overlay"]}>
                    <div className={classes["card__header"]}>
                      {/* <svg className={classes["card__arc"]}>
                        <path />
                      </svg> */}
                      <div className={classes["card__header-text"]}>
                        <h3 className={classes["card__title"]}>
                          {users[keyName].team}
                        </h3>
                        <p className={classes["card__fullname"]}>
                          - {users[keyName].fullName} -
                        </p>
                        <span className={classes["card__mln-left"]}>
                          <span className={classes.mln}>
                            {getAmountLeftForPlayer(
                              keyName,
                              players,
                              settings.user_mln
                            )}
                            <span
                              style={{ marginLeft: "1px" }}
                              className="marketCurrency"
                            >
                              mln
                            </span>
                          </span>
                        </span>
                      </div>
                    </div>
                    <p className={classes["card__description"]}>
                      {getAmountOfPlayerLeftToBuyForUser(
                        keyName,
                        players,
                        settings.min_players
                      )}{" "}
                      giocatori rimanenti
                    </p>
                  </div>
                </div>
              </div>
            );
          })}
          {Object.keys(users).length === 0 && (
            <p style={{ textAlign: "center" }}>
              Non hai ancora creato nessun utente.
            </p>
          )}
        </div>
      </div>
    </div>
  );
};

export default PlayerList;
