// Import react dependecies
import { Select, MenuItem, Switch } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import React, { useState, useRef } from "react";

// Import classes
import classes from "./GeneralSettings.module.css";

const GeneralSettings = (props) => {
  // add useselector hook for data
  const settings = useSelector((state) => state.settings);

  // Set hook for inputs
  const [gameType, setGameType] = React.useState(settings["game_type"]);
  const [timer, setTimer] = React.useState(settings.timer);
  const [randomShuffle, setRandomShuffle] = React.useState(
    settings["random_shuffle"]
  );

  // useRef hook
  const maxPlayers = useRef(settings.max_players);
  const minPlayers = useRef(settings.min_players);
  const timerValue = useRef(settings.timer_value);
  const def = useRef(settings.random_shuffle_settings.def);
  const cen = useRef(settings.random_shuffle_settings.cen);
  const atk = useRef(settings.random_shuffle_settings.atk);
  const [formIsValid, setFormIsValid] = useState(true);
  const [errorString, setErrorString] = useState("");
  const [successString, setSuccessString] = useState("");

  // add distaptch
  const dispatch = useDispatch();

  // Handle select onChange
  const handleGameTypeChange = (event) => {
    setGameType(event.target.value);
    setFormIsValid(true);
    setErrorString("");
    setSuccessString("");
  };

  // handle timer switch onChange
  const handleTimerChange = () => {
    setTimer(!timer);
    setFormIsValid(true);
    setErrorString("");
    setSuccessString("");
  };

  // handle random shuffle switch onChange
  const handleRandomShuffleChange = () => {
    setRandomShuffle(!randomShuffle);
    setFormIsValid(true);
    setErrorString("");
    setSuccessString("");
  };

  // Handle inputs validity
  const onHandleFormInputs = () => {
    setFormIsValid(true);
    setErrorString("");
    setSuccessString("");
  };

  // reset users
  const onResetUsers = () => {
    if (window.confirm(`Sei sicuro di eliminare tutti gli utenti? Questa operazione sarà irreversibile.`)) {
      dispatch({ type: "reset_users" });
    }
  };

  // reset players
  const onResetPlayers = () => {
    if (window.confirm(`Sei sicuro di voler cancellare i dati relativi ai calciatori? (possessore e contatore shuffle)`)) {
      dispatch({ type: "reset_players" });
    }
  };

  // submitForm
  const onSubmitForm = (event) => {
    event.preventDefault();
    let maxPlayersValue = Math.trunc(maxPlayers.current.value);
    let minPlayersValue = Math.trunc(minPlayers.current.value);
    let timerValueValue = Math.trunc(
      timerValue.current ? timerValue.current.value : 0
    );
    let defValue = Math.trunc(def.current ? def.current.value : 0);
    let cenValue = Math.trunc(cen.current ? cen.current.value : 0);
    let atkValue = Math.trunc(atk.current ? atk.current.value : 0);
    let gameTypeValue = gameType;
    let timerInputValue = timer;
    let randomShuffleValue = randomShuffle;

    // validate Input
    if (maxPlayersValue < 0) {
      setFormIsValid(false);
      setErrorString(
        "Inseirisci un valore valido per il numero massimo di giocatori"
      );
      return;
    } else if (minPlayersValue < 0 || minPlayersValue > maxPlayersValue) {
      setFormIsValid(false);
      setErrorString(
        "Inseirisci un valore valido per il numero minimo di giocatori"
      );
      return;
    } else if (timerValueValue < 0 || timerValueValue > 600) {
      setFormIsValid(false);
      setErrorString("Inseirisci un valore valido per tempo del timer");
      return;
    } else if (defValue < 0 && randomShuffleValue) {
      setFormIsValid(false);
      setErrorString("Inseirisci un valore valido per i difensori");
      return;
    } else if (cenValue < 0 && randomShuffleValue) {
      setFormIsValid(false);
      setErrorString("Inseirisci un valore valido per i centrocampisti");
      return;
    } else if (atkValue < 0 && randomShuffleValue) {
      setFormIsValid(false);
      setErrorString("Inseirisci un valore valido per gli attaccanti");
      return;
    }

    // Clear data caused by switching
    if (!timerInputValue) {
      timerValueValue = 0;
    }
    if (!randomShuffleValue) {
      defValue = 0;
      cenValue = 0;
      atkValue = 0;
    }

    // dispatch data
    dispatch({
      type: "change_settings",
      data: {
        maxPlayers: maxPlayersValue,
        minPlayers: minPlayersValue,
        timerValue: timerValueValue,
        def: defValue,
        cen: cenValue,
        atk: atkValue,
        gameType: gameTypeValue,
        timer: timerInputValue,
        randomShuffle: randomShuffleValue,
      },
    });

    setSuccessString('Impostazioni salvate con successo!')
  };

  return (
    <form onSubmit={onSubmitForm}>
      <h3>Impostazioni generali</h3>
      <div className={classes.wrapper}>
        <div className={classes.block}>
          <h4>Impostazioni di gioco</h4>

          <div className={classes.fieldWrapper}>
            <label htmlFor="n_max_players" className={classes.inp}>
              <input
                type="number"
                step="1"
                min="1"
                id="n_max_players"
                name="n_max_players"
                placeholder="&nbsp;"
                defaultValue={settings.max_players}
                ref={maxPlayers}
                onKeyPress={onHandleFormInputs}
              />
              <span className={classes.label}>N° max giocatori</span>
              <span className={classes["focus-bg"]}></span>
            </label>
          </div>

          <div className={classes.fieldWrapper}>
            <label htmlFor="n_min_players" className={classes.inp}>
              <input
                type="number"
                step="1"
                min="1"
                id="n_min_players"
                name="n_min_players"
                placeholder="&nbsp;"
                defaultValue={settings.min_players}
                ref={minPlayers}
                onKeyPress={onHandleFormInputs}
              />
              <span className={classes.label}>N° min giocatori</span>
              <span className={classes["focus-bg"]}></span>
            </label>
          </div>

          <div className={classes.fieldWrapper}>
            <label htmlFor="mln" className={classes.inp}>
              <input
                type="number"
                step="1"
                min="1"
                id="mln"
                name="mln"
                placeholder="&nbsp;"
                defaultValue={settings.user_mln}
                onKeyPress={onHandleFormInputs}
              />
              <span className={classes.label}>Milioni per fantallenatore</span>
              <span className={classes["focus-bg"]}></span>
            </label>
          </div>

          <div className={classes.fieldWrapper}>
            <label className={classes["custom-label"]}>Tipo di gioco</label>
            <Select
              labelId="game_type_label"
              id="game_typet"
              value={gameType}
              onChange={handleGameTypeChange}
              style={{ width: "100%" }}
            >
              <MenuItem value={"classic"}>Classic</MenuItem>
              <MenuItem value={"mantra"}>Mantra</MenuItem>
            </Select>
          </div>

        </div>


        <div className={classes.block}>
          <h4>Impostazioni di chiamata random</h4>
          <div className={classes.fieldWrapper}>
            <label className={classes["custom-label"]}>
              impostazioni di chiamata random personalizzate
            </label>
            <div>
              <span>Off</span>
              <Switch
                key={`switch-${randomShuffle}`} /* fixed issue */
                value="A"
                defaultChecked={randomShuffle}
                onChange={handleRandomShuffleChange}
              />
              <span>On</span>
            </div>
          </div>
          {randomShuffle && (
            <>
              <label className={classes["custom-label"]}>
                In questa sezione puoi personalizzare le impostazioni di
                chiamata random (bottone ESTRAI). In ogni campo sottostante puoi decidere
                il numero di acquisti (per ruolo) necessari per sbloccare l'estrazione del ruolo successivo (P➝D➝C➝A). <br />
                Es: impostando D: 10, dovranno essere acquistati almeno 10 P per estrarre il primo giocatore D.
              </label>
              <div className={classes.fieldWrapper}></div>
              <div className={classes.fieldWrapper}>
                <label htmlFor="dif" className={classes.inp}>
                  <input
                    type="number"
                    step="1"
                    min="0"
                    id="dif"
                    name="dif"
                    placeholder="&nbsp;"
                    ref={def}
                    onKeyPress={onHandleFormInputs}
                    defaultValue={
                      settings.random_shuffle_settings.def
                        ? settings.random_shuffle_settings.def
                        : 0
                    }
                  />
                  <span className={classes.label}>pre Difensori (D)</span>
                  <span className={classes["focus-bg"]}></span>
                </label>
              </div>
              <div className={classes.fieldWrapper}>
                <label htmlFor="cen" className={classes.inp}>
                  <input
                    type="number"
                    step="1"
                    min="0"
                    id="cen"
                    name="cen"
                    placeholder="&nbsp;"
                    ref={cen}
                    onKeyPress={onHandleFormInputs}
                    defaultValue={
                      settings.random_shuffle_settings.cen
                        ? settings.random_shuffle_settings.cen
                        : 0
                    }
                  />
                  <span className={classes.label}>pre Centrocampisti (C)</span>
                  <span className={classes["focus-bg"]}></span>
                </label>
              </div>
              <div className={classes.fieldWrapper}>
                <label htmlFor="atk" className={classes.inp}>
                  <input
                    type="number"
                    step="1"
                    min="0"
                    id="atk"
                    name="atk"
                    placeholder="&nbsp;"
                    ref={atk}
                    onKeyPress={onHandleFormInputs}
                    defaultValue={
                      settings.random_shuffle_settings.atk
                        ? settings.random_shuffle_settings.atk
                        : 0
                    }
                  />
                  <span className={classes.label}>pre Attaccanti (A)</span>
                  <span className={classes["focus-bg"]}></span>
                </label>
              </div>
            </>
          )}
        </div>

        <div className={classes.block}>
          <h4>Ripristina impostazioni</h4>
          <div className={classes.fieldWrapper}>
            <button
              type="button"
              className="danger"
              style={{ width: "100%" }}
              onClick={onResetUsers}
            >
              <svg
                version="1.1"
                width="12"
                height="12"
                x="0"
                y="0"
                viewBox="0 0 329.26933 329"
                style={{ marginRight: "8px" }}
              >
                <g>
                  <path
                    xmlns="http://www.w3.org/2000/svg"
                    d="m194.800781 164.769531 128.210938-128.214843c8.34375-8.339844 8.34375-21.824219 0-30.164063-8.339844-8.339844-21.824219-8.339844-30.164063 0l-128.214844 128.214844-128.210937-128.214844c-8.34375-8.339844-21.824219-8.339844-30.164063 0-8.34375 8.339844-8.34375 21.824219 0 30.164063l128.210938 128.214843-128.210938 128.214844c-8.34375 8.339844-8.34375 21.824219 0 30.164063 4.15625 4.160156 9.621094 6.25 15.082032 6.25 5.460937 0 10.921875-2.089844 15.082031-6.25l128.210937-128.214844 128.214844 128.214844c4.160156 4.160156 9.621094 6.25 15.082032 6.25 5.460937 0 10.921874-2.089844 15.082031-6.25 8.34375-8.339844 8.34375-21.824219 0-30.164063zm0 0"
                    fill="#eb1717"
                    data-original="#000000"
                  />
                </g>
              </svg>
              Resetta Fantallenatori
            </button>
          </div>
          <div className={classes.fieldWrapper}>
            <button
              type="button"
              className="danger"
              style={{ width: "100%" }}
              onClick={onResetPlayers}
            >
              <svg
                version="1.1"
                width="12"
                height="12"
                x="0"
                y="0"
                viewBox="0 0 329.26933 329"
                style={{ marginRight: "8px" }}
              >
                <g>
                  <path
                    xmlns="http://www.w3.org/2000/svg"
                    d="m194.800781 164.769531 128.210938-128.214843c8.34375-8.339844 8.34375-21.824219 0-30.164063-8.339844-8.339844-21.824219-8.339844-30.164063 0l-128.214844 128.214844-128.210937-128.214844c-8.34375-8.339844-21.824219-8.339844-30.164063 0-8.34375 8.339844-8.34375 21.824219 0 30.164063l128.210938 128.214843-128.210938 128.214844c-8.34375 8.339844-8.34375 21.824219 0 30.164063 4.15625 4.160156 9.621094 6.25 15.082032 6.25 5.460937 0 10.921875-2.089844 15.082031-6.25l128.210937-128.214844 128.214844 128.214844c4.160156 4.160156 9.621094 6.25 15.082032 6.25 5.460937 0 10.921874-2.089844 15.082031-6.25 8.34375-8.339844 8.34375-21.824219 0-30.164063zm0 0"
                    fill="#eb1717"
                    data-original="#000000"
                  />
                </g>
              </svg>
              Resetta Calciatori
            </button>
          </div>
        </div>
      </div>
      {!formIsValid && <p className={classes.error}>{errorString}</p>}
      {successString !== '' && formIsValid && <p className={classes.success}>{successString}</p>}
      <div style={{ marginTop: "10px", display: "flex", marginLeft: "2px" }}>
        <button className="primaryButton" type="submit">
          Salva
        </button>
      </div>
    </form>
  );
};

export default GeneralSettings;
