// trannform inot a slug a given string
const slugyfy = (str) => {
  str = str.replace(/^\s+|\s+$/g, ""); // trim
  str = str.toLowerCase();

  // remove accents, swap ñ for n, etc
  var from = "àáäâèéëêìíïîòóöôùúüûñç·/_,:;";
  var to = "aaaaeeeeiiiioooouuuunc------";
  for (var i = 0, l = from.length; i < l; i++) {
    str = str.replace(new RegExp(from.charAt(i), "g"), to.charAt(i));
  }

  str = str
    .replace(/[^a-z0-9 -]/g, "") // remove invalid chars
    .replace(/\s+/g, "-") // collapse whitespace and replace by -
    .replace(/-+/g, "-"); // collapse dashes

  return str;
};

// Generate random ID
const generateID = () => {
  return Math.random().toString(36).substr(2, 14);
};

// Select random image from given set and params
const selectRandomImageFromSet = (
  obj,
  startingOwnedParam,
  numberOfTentatives
) => {
  let keys = Object.keys(obj);
  let randomIndex = keys[Math.floor(Math.random() * keys.length)];
  let item = obj[randomIndex];
  let tentatives = numberOfTentatives;
  let ownedParam = startingOwnedParam;
  if (item.owned > ownedParam) {
    // if the image is already owned by someone,
    // Check if the number of tentatives is the same of the object lenght.
    // this means that there are not available images and we need to pick up a copy
    if (tentatives === keys.length) {
      // We tried all the elements. Time to start with a copy image
      ownedParam++;
      return selectRandomImageFromSet(obj, ownedParam, 0);
    } else {
      // Just look for anothe item
      tentatives++;
      return selectRandomImageFromSet(obj, ownedParam, tentatives);
    }
  }
  return item;
};

// get amount (MLN) left for given user (player key)
const getAmountLeftForPlayer = (playerKey, players, maxAmount) => {
  // Cicle through the object of players and get the purchased amount
  let currentAmount = 0;
  Object.keys(players).map(function (keyName, keyIndex) {
    if (players[keyName].purchased_by === playerKey) {
      return (currentAmount += players[keyName].purchased_for);
    }
  });
  return maxAmount - currentAmount;
};

// get max offer (MLN) for given user (player key)
const getMaxOfferFromUser = (
  playerKey,
  players,
  maxAmount,
  minNumOfPlayers
) => {
  if (!minNumOfPlayers) {
    // from settings, there is not a minimum number of purchasable players.
    return getAmountLeftForPlayer(playerKey, players, maxAmount);
  }
  // Cicle through the object of players and get the purchased amount
  let currentAmount = 0;
  let currentNumOfPlayers = 0;
  Object.keys(players).map(function (keyName, keyIndex) {
    if (players[keyName].purchased_by === playerKey) {
      currentAmount += players[keyName].purchased_for;
      currentNumOfPlayers += 1;
      return;
    }
  });
  return (
    maxAmount - currentAmount - (minNumOfPlayers - currentNumOfPlayers - 1)
  );
};

// get amount of players (#) left to buy for given user (player key)
const getAmountOfPlayerLeftToBuyForUser = (
  playerKey,
  players,
  minNumOfPlayers
) => {
  // Cicle through the object of players and get the purchased players number
  let currentAmount = 0;
  Object.keys(players).map(function (keyName, keyIndex) {
    if (players[keyName].purchased_by === playerKey) {
      return (currentAmount += 1);
    }
  });
  return minNumOfPlayers - currentAmount;
};

