// Import react dependecies
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import FilterableTable from "react-filterable-table";
import { useState, useEffect } from "react";

// Import classes
import classes from "./PlayerTable.module.css";
import "../UI/table.css";

// Import helpers
import { shuffle } from "../../Helpers/helpers";

//Import images
// import downloadImg from '../../Assets/icons/download.svg'

const PlayerTable = (props) => {
  const players = useSelector((state) => state.players);
  const users = useSelector((state) => state.users);
  const settings = useSelector((state) => state.settings);

  const [playerSubset, setPlayerSubset] = useState();

  // Get subset of players
  useEffect(() => {
    setPlayerSubset([...getPlayersSubset(players)]);
    localStorage.setItem("activeRoleFilters", []);
  }, [players]);

  // add distaptch
  const dispatch = useDispatch();

  // function to render the Players name capitalizde
  const renderName = (props) => {
    let namesArray = props.value.split(" ");
    namesArray.forEach((element, index) => {
      namesArray[index] =
        namesArray[index].charAt(0) + namesArray[index].slice(1).toLowerCase();
    });
    const stringName = namesArray.join(" ");
    return (
      <span className={classes.bold} title={props.value}>
        {stringName}
      </span>
    );
  };

  // function to render the roles badges
  const renderRoles = (props) => {
    let { addExactFilter, field } = props;
    // setRoleFIlters();
    if (settings.game_type === "mantra") {
      const rolesAry = props.value.split(";");
      return (
        <span style={{ display: "flex" }}>
          {rolesAry.map((element) => {
            return (
              <span
                key={props.record.id + element.toLowerCase()}
                className={"roleBadge " + element.toLowerCase()}
              >
                {element}
              </span>
            );
          })}
        </span>
      );
    } else {
      return (
        <span style={{ display: "flex" }}>
          <span
            onClick={() => addExactFilter(props.record.role, "role", "role")}
            key={props.record.id + props.record.role.toLowerCase()}
            className={"roleBadge " + props.record.role.toLowerCase()}
          >
            {props.record.role}
          </span>
        </span>
      );
    }
  };

  // function to render purchased player
  const renderPurchasedUser = (props) => {
    return (
      <span
        style={{ fontSize: "13px" }}
        className={
          props.value === "" || !props.value
            ? `${classes.green} ${classes.bold}`
            : ""
        }
      >
        {props.value === "" || !props.value || !users[props.value]
          ? "Libero"
          : users[props.value].team}
      </span>
    );
  };

  // Get logged user
  const fantauser = JSON.parse(window.localStorage.getItem("fantauser"));

  // Render button
  const renderButton = (data) => {
    let content = "";
    if (data.record.purchased_by === "" || !data.record.purchased_by) {
      content = (
        <button
          className="primaryButton small"
          disabled={fantauser && fantauser.role === "admin" ? false : true}
          onClick={() => {
            props.onSelectOption({
              action: "selectPlayer",
              isShown: true,
              id: data.value,
            });
          }}
        >
          <svg
            viewBox="0 0 511.999 511.999"
            width="15"
            height="15"
            style={{ marginRight: "5px" }}
          >
            <path d="M117.776 174.345a10.01 10.01 0 0 0-14.139 0 9.998 9.998 0 1 0 14.139 14.14c3.909-3.9 3.909-10.23 0-14.14zm135.423 273.373c4.409-5.972 6.792-13.167 6.792-20.732 0-19.298-15.7-34.999-34.999-34.999H54.998a34.76 34.76 0 0 0-24.749 10.249 34.76 34.76 0 0 0-10.25 24.75c0 7.749 2.536 14.915 6.815 20.72C10.891 456.078 0 472.778 0 491.983v10c0 5.523 4.478 10 10 10h259.99c5.522 0 10-4.477 10-10v-10c0-19.196-10.88-35.889-26.791-44.265zM44.39 416.376a14.9 14.9 0 0 1 10.608-4.391h169.994a15.02 15.02 0 0 1 14.999 14.999 14.9 14.9 0 0 1-15 15H54.998a15.02 15.02 0 0 1-14.999-14.999c0-4.009 1.559-7.777 4.391-10.609zm-24.391 75.607c0-16.541 13.458-29.999 29.999-29.999h5 169.994 5c16.541 0 29.999 13.458 29.999 29.999H19.999zm482.694-71.859c-3.396-5.263-7.448-9.917-12.045-13.833l-46.725-40.753a10.07 10.07 0 0 0-1.269-1.551c-.812-.811-1.732-1.444-2.71-1.918l-28.257-24.645-130.79-114.091a29.75 29.75 0 0 0 8.302-20.699 29.81 29.81 0 0 0-8.794-21.225 29.69 29.69 0 0 0-8.291-5.837l12.1-12.1c3.382.899 6.857 1.359 10.335 1.359 10.241-.001 20.483-3.899 28.279-11.695 7.558-7.557 11.72-17.602 11.72-28.285s-4.162-20.728-11.719-28.285L237.981 11.72A39.74 39.74 0 0 0 209.692.001c-10.681 0-20.724 4.162-28.28 11.719-10.444 10.444-13.882 25.273-10.336 38.615L50.351 171.059c-3.336-.887-6.807-1.355-10.353-1.355-10.688 0-20.729 4.159-28.28 11.709-15.596 15.596-15.596 40.973 0 56.57l84.847 84.848c7.799 7.798 18.042 11.697 28.285 11.697s20.487-3.899 28.286-11.697c10.444-10.445 13.882-25.273 10.336-38.615l12.075-12.076c1.451 3.03 3.42 5.826 5.866 8.271a29.8 29.8 0 0 0 21.215 8.787 29.79 29.79 0 0 0 20.696-8.291L362.63 440.961c.384.6.832 1.171 1.356 1.695a9.76 9.76 0 0 0 .558.504l41.511 47.694a58.96 58.96 0 0 0 3.28 3.561c11.401 11.397 26.46 17.582 42.27 17.582 2.787 0 5.6-.192 8.417-.582 18.829-2.606 35.102-13.87 44.644-30.908 10.453-18.673 9.675-42.38-1.973-60.383zM195.555 25.862c3.779-3.779 8.801-5.861 14.138-5.861 5.344 0 10.369 2.082 14.147 5.861l84.848 84.847a19.87 19.87 0 0 1 5.86 14.143c0 5.341-2.081 10.364-5.861 14.143-7.795 7.797-20.481 7.797-28.276 0L195.56 54.144c-7.802-7.801-7.802-20.486-.005-28.282zm-56.561 282.827a20.03 20.03 0 0 1-28.287 0L25.86 223.842c-7.799-7.799-7.799-20.487 0-28.287 3.772-3.773 8.794-5.851 14.138-5.851s10.362 2.077 14.133 5.847l84.862 84.862c7.798 7.795 7.798 20.481.001 28.276zm42.102-70.38l-27.961 27.962-84.856-84.856L181.412 68.28l84.855 84.856-28.012 28.013-.276.265-56.538 56.539c-.117.115-.23.235-.345.356zm35.706 20.861l-.059.059-7.042 7.042a9.94 9.94 0 0 1-7.074 2.928c-2.673 0-5.185-1.04-7.073-2.929a9.94 9.94 0 0 1-2.932-7.079c0-2.618.999-5.081 2.812-6.939l56.801-56.801c3.893-3.786 10.187-3.739 14.034.104 1.89 1.89 2.931 4.404 2.931 7.079s-1.038 5.18-2.932 7.069l-7.001 7.001-.143.143-42.322 42.323zm20.708 7.575l29.226-29.226 123.742 107.935-45.154 45.155L237.51 266.745zm120.975 138.986l47.099-47.1 15.107 13.177-49.045 49.046-13.161-15.123zm128.732 65.006c-6.454 11.523-17.365 19.128-29.936 20.868-12.542 1.736-24.865-2.395-33.807-11.333a38.42 38.42 0 0 1-2.191-2.381l-.097-.113-36.38-41.802 50.991-50.992 41.75 36.417c.031.028.063.055.096.083 3.125 2.657 5.898 5.848 8.251 9.494 7.562 11.685 8.092 27.665 1.323 39.759zM188.484 103.636c-3.906-3.905-10.236-3.905-14.142 0l-42.428 42.428a10 10 0 0 0 0 14.141c1.953 1.953 4.512 2.929 7.071 2.929a9.97 9.97 0 0 0 7.071-2.929l42.428-42.428a10 10 0 0 0 0-14.141z" />
          </svg>
          Asta
        </button>
      );
    } else {
      content = (
        <button
          className="primaryButton small"
          onClick={() => {
            props.onSelectOption({
              action: "selectPlayer",
              isShown: true,
              id: data.value,
            });
          }}
        >
          <svg
            viewBox="0 0 512.005 512.005"
            width="15"
            height="15"
            style={{ marginRight: "5px" }}
          >
            <path d="M505.749 475.587l-145.6-145.6c28.203-34.837 45.184-79.104 45.184-127.317C405.333 90.926 314.41.003 202.666.003S0 90.925 0 202.669s90.923 202.667 202.667 202.667c48.213 0 92.48-16.981 127.317-45.184l145.6 145.6c4.16 4.16 9.621 6.251 15.083 6.251s10.923-2.091 15.083-6.251c8.341-8.341 8.341-21.824-.001-30.165zM202.667 362.669c-88.235 0-160-71.765-160-160s71.765-160 160-160 160 71.765 160 160-71.766 160-160 160z" />
          </svg>
          Dettaglio
        </button>
      );
    }
    return content;
  };

  // function to render The team
  const renderTeam = (props) => {
    const src = require("../../Assets/stemmi/" + props.record.team + ".png");
    return (
      <span style={{ fontSize: "13px", display: "flex", alignItems: "center" }}>
        <img src={src.default} className="smallTeamLogo" />
        {props.value}
      </span>
    );
  };

  // inti const field
  const fields = [
    {
      name: "image",
      visible: false,
    },
    // {
    //   name: (settings && settings.game_type === "mantra") ? "role" : "macrorole",
    //   visible: false,
    // },
    {
      name: "purchased_in",
      visible: false,
    },
    {
      name: "name",
      displayName: "Nome",
      inputFilterable: true,
      sortable: true,
      render: renderName,
    },
    {
      name: "team",
      displayName: "Squadra",
      inputFilterable: true,
      exactFilterable: true,
      sortable: true,
      render: renderTeam,
    },
    {
      name: "role",
      displayName: "Ruolo",
      inputFilterable: true,
      sortable: true,
      render: renderRoles,
    },
    {
      name: "purchased_by",
      displayName: "Proprietario",
      inputFilterable: true,
      exactFilterable: true,
      sortable: true,
      render: renderPurchasedUser,
    },
    {
      name: "id",
      displayName: "Azione",
      render: renderButton,
    },
  ];

  // init shuffle
  const shufflePlayer = () => {
    const shuffledPlayer = shuffle(players, settings);

    props.onSelectOption({
      action: "selectPlayer",
      isShown: true,
      id: shuffledPlayer.id,
    });

    // Update player shuffled_times
    dispatch({
      type: "shuffle_update",
      playerId: shuffledPlayer.id,
    });
  };

  const getPlayersSubset = (players) => {
    if (Object.keys(players).length) {
      const playersSubset = [];
      Object.keys(players).forEach(function (key) {
        playersSubset.push({
          id: players[key].id,
          name: players[key].name,
          team: players[key].team,
          role:
            settings && settings.game_type === "mantra"
              ? players[key].role
              : players[key].macrorole,
          purchased_by: players[key].purchased_by,
        });
      });
      return playersSubset;
    } else {
      return [];
    }
  };

  // Set role filters
  const setRoleFIlters = () => {
    let wrapper = document.getElementsByClassName("filter-container")[0];
    if (wrapper) {
      // Remove all the .rolefilter-wrapper elements
      document
        .querySelectorAll(".rolefilter-wrapper")
        .forEach(function (element) {
          element.remove();
        });
      // Create role filters wraoper
      let div = document.createElement("div");
      div.className = "rolefilter-wrapper";
      wrapper.appendChild(div);

      // Now create the filyters inside the div
      let roles = [];
      if (settings.game_type === "mantra") {
        roles = ["Por", "Dc", "Dd", "Ds", "E", "M", "C", "W", "T", "A", "Pc"];
      } else {
        roles = ["P", "D", "C", "A"];
      }
      roles.forEach((element) => {
        let span = document.createElement("span");
        const activeFIlters = localStorage.getItem("activeRoleFilters");
        span.className =
          "roleBadge filterRoleBadge " +
          element.toLowerCase() +
          (activeFIlters && activeFIlters.includes(element)
            ? " active"
            : " unactive");
        span.setAttribute("role", element);
        span.textContent = element.toUpperCase();
        span.onclick = function () {
          if (this.classList.contains("unactive")) {
            this.classList.remove("unactive");
            this.classList.add("active");
          } else {
            this.classList.remove("active");
            this.classList.add("unactive");
          }
          filterForRole();
        };
        div.appendChild(span);
      });
    }
  };

  // Set download JSON
  const download = () => {
    // Get JSON array to download
    let jsonObj = {};
    Object.keys(players).map(function (keyName, keyIndex) {
      if (players[keyName].purchased_by !== "") {
        const user = users[players[keyName].purchased_by].team;
        if (jsonObj.hasOwnProperty(user)) {
          console.log(players[keyName].name + " esiste. Pusho");
          jsonObj[user].push(
            players[keyName].name.charAt(0) +
              players[keyName].name.slice(1).toLowerCase() +
              " " +
              players[keyName].team +
              " " +
              players[keyName].role +
              " " +
              players[keyName].purchased_for
          );
        } else {
          jsonObj[user] = [];
          jsonObj[user] = [
            players[keyName].name.charAt(0) +
              players[keyName].name.slice(1).toLowerCase() +
              " " +
              players[keyName].team +
              " " +
              players[keyName].role +
              " " +
              players[keyName].purchased_for,
          ];
        }
      }
    });

    // jsonObj = {
    //   'Porcellona': {
    //     nome: ["handanovic", "Meret"],
    //     squadra: ["inter", "Napoli"],
    //     ruolo: ["POR", "M;C"],
    //     prezzo: [50, 60]
    //   },
    //   "Fc Devo Pensare": {
    //     nome: ["Zio", "Porco"],
    //     squadra: ["inter", "Napoli"],
    //     ruolo: ["POR", "M;C"],
    //     prezzo: [50, 60]
    //   },
    // };

    var dataStr =
      "data:text/json;charset=utf-8," +
      encodeURIComponent(JSON.stringify(jsonObj));
    var dlAnchorElem = document.getElementById("downloadAnchorElem");
    dlAnchorElem.setAttribute("href", dataStr);
    dlAnchorElem.setAttribute("download", "tabella.json");
    dlAnchorElem.click();
  };

  const filterForRole = (role) => {
    let rolesToBeFiltered = [];
    document
      .querySelectorAll(".filterRoleBadge.active")
      .forEach(function (element) {
        rolesToBeFiltered.push(element.getAttribute("role"));
      });

    localStorage.setItem("activeRoleFilters", rolesToBeFiltered);

    if (rolesToBeFiltered.length) {
      const roleField = settings.game_type === 'mantra' ? 'role' : 'macrorole';
      const newPlayers = Object.keys(players)
        .filter((key) =>
          players[key][roleField]
            .split(";")
            .some((r) => rolesToBeFiltered.indexOf(r) >= 0)
        )
        .reduce((obj, key) => {
          obj[key] = players[key];
          return obj;
        }, {});
        setPlayerSubset([...getPlayersSubset(newPlayers)]);
    } else {
      setPlayerSubset([...getPlayersSubset(players)]);
    }


    // players.filter((element) => {
    //   return element.role === "E";
    // });
  };

  return (
    <div className={classes.wrapper}>
      <div className={classes.container}>
        <div className={classes.sectionTitle}>
          <h2>Lista calciatori</h2>
        </div>
        {fantauser && fantauser.role === "admin" && (
          <div
            style={{
              marginTop: "10px",
              marginBottom: "10px",
              paddingLeft: "5px",
              display: "flex",
            }}
          >
            <button
              className="shuffleButton"
              onClick={shufflePlayer}
              title="Estrai un giocatore secondo i criteri stabiliti"
            >
              Estrai
              <svg
                width="15"
                height="15"
                viewBox="0 0 512 512"
                style={{ marginLeft: "5px" }}
              >
                <path d="M506.24 371.7l-96-80c-4.768-4-11.424-4.8-17.024-2.208A16.02 16.02 0 0 0 384 303.988v48h-26.784c-22.208 0-42.496-11.264-54.272-30.08l-103.616-165.76c-23.52-37.664-64.096-60.16-108.544-60.16H0v64h90.784c22.208 0 42.496 11.264 54.272 30.08l103.616 165.76c23.552 37.664 64.128 60.16 108.544 60.16H384v48a16.02 16.02 0 0 0 9.216 14.496c2.144.992 4.48 1.504 6.784 1.504 3.68 0 7.328-1.248 10.24-3.712l96-80c3.68-3.04 5.76-7.552 5.76-12.288s-2.08-9.248-5.76-12.288zm0-256l-96-80c-4.768-3.968-11.424-4.8-17.024-2.176C387.584 36.116 384 41.78 384 47.988v48h-26.784c-44.448 0-85.024 22.496-108.544 60.16l-5.792 9.28 37.728 60.384 22.336-35.744c11.776-18.816 32.064-30.08 54.272-30.08H384v48c0 6.208 3.584 11.872 9.216 14.496 2.144.992 4.48 1.504 6.784 1.504 3.68 0 7.328-1.28 10.24-3.712l96-80c3.68-3.04 5.76-7.552 5.76-12.288s-2.08-9.248-5.76-12.288zM167.392 286.164l-22.304 35.744c-11.776 18.816-32.096 30.08-54.304 30.08H0v64h90.784c44.416 0 84.992-22.496 108.544-60.16l5.792-9.28-37.728-60.384z" />
              </svg>
            </button>
            <button
              className="shuffleButton"
              onClick={download}
              title="Scarica in formato JSON il file degli acquisti"
              style={{
                marginLeft: "10px",
              }}
            >
              Download JSON
            </button>
            <a id="downloadAnchorElem" style={{ display: "none" }}></a>
          </div>
        )}
        {playerSubset && (
          <FilterableTable
            namespace="Calciatori"
            initialSort="name"
            data={playerSubset}
            fields={fields}
            noRecordsMessage="Non ci sono calciatori che soddisfano i tuoi criteri."
            noFilteredRecordsMessage="Non ci sono calciatori che soddisfano i tuoi criteri."
            recordCountName="risultato"
            recordCountNamePlural="risultati"
            pageSize="10"
            pagerTitles={{ first: "Primo", last: "Ultimo" }}
            iconSortAsc={"↑"}
            iconSort={" "}
            iconSortedDesc={"↓"}
            topPagerVisible={false}
          />
        )}
        {playerSubset && setRoleFIlters()}
      </div>
    </div>
  );
};

export default PlayerTable;
