const generateNewState = (state) => {
  return {
    ...state,
    users: {
      ...state.users,
    },
    players: {
      ...state.players,
    },
    settings: {
      ...state.settings,
    },
  };
};

// Push the datat into the server
const pushToServer = (newState) => {
  const fetchPromise = fetch(
    "https://fantaasta-37ac8-default-rtdb.europe-west1.firebasedatabase.app/settings/-McA5xpKzx99xHhBWC0W.json",
    {
      method: "PUT",
      body: JSON.stringify(newState),
      headers: {
        "Content-Type": "application/json",
      },
    }
  ).then((response) => {
    console.log(response);
    //@todo handle failure
  });
};

//set timer as for the updates on key stroke
let timer;

const settingsReducer = (state = initialState, action) => {
  let newState = generateNewState(state);
  switch (action.type) {
    case "add_user":
      // Add user
      newState.users[action.userKey] = action.newUser;
      // update userImage owned
      let elementIndex;
      for (let i = 0; i < newState.settings.userImagesSamples.length; i += 1) {
        if (newState.settings.userImagesSamples[i].id === action.imageId) {
          elementIndex = i;
        }
      }
      newState.settings.userImagesSamples[elementIndex].owned =
        newState.settings.userImagesSamples[elementIndex].owned + 1;
      pushToServer(newState);
      return newState;
      break;
    case "update_user":
      // update given field by accessing given key
      newState.users[action.key][action.field] = action.newValue;
      clearTimeout(timer);
      timer = setTimeout(() => {
        pushToServer(newState);
      }, 3000);
      return newState;
      break;
    case "delete_user":
      // Clean from the players this user
      Object.keys(newState.players).map(function (keyName, keyIndex) {
        if (newState.players[keyName].purchased_by === action.key) {
          newState.players[keyName].purchased_by = "";
          newState.players[keyName].purchased_for = "";
          newState.players[keyName].shuffled_times = 0;
        }
      });
      // Clean from the imageSet this user
      const imageName = newState.users[action.key].imageName;
      Object.keys(newState.settings.userImagesSamples).map(function (
        keyName,
        keyIndex
      ) {
        if (newState.settings.userImagesSamples[keyName].name === imageName) {
          newState.settings.userImagesSamples[keyName].owned = 0;
        }
      });

      //Delete user
      delete newState.users[action.key];
      pushToServer({});
      pushToServer(newState);
      return newState;
      break;
    case "pull_settings":
      return {
        ...action.data,
        users: {
          ...action.data.users,
        },
        players: {
          ...action.data.players,
        },
        settings: {
          ...action.data.settings,
        },
      };
      break;
    case "reset_players":
      Object.keys(newState.players).map(function (keyName, keyIndex) {
        if (newState.players[keyName].purchased_by !== "") {
          newState.players[keyName].purchased_by = "";
          newState.players[keyName].purchased_for = "";
          newState.players[keyName].shuffled_times = 0;
        }
      });
      pushToServer(newState);
      return newState;
      break;
    case "reset_users":
      // remove everything user-related from players
      // Clean from the players this user
      Object.keys(newState.players).map(function (keyName, keyIndex) {
        if (newState.players[keyName].purchased_by !== "") {
          newState.players[keyName].purchased_by = "";
          newState.players[keyName].purchased_for = "";
          newState.players[keyName].shuffled_times = 0;
        }
      });
      // Clean from the imageSet this user
      Object.keys(newState.settings.userImagesSamples).map(function (
        keyName,
        keyIndex
      ) {
        newState.settings.userImagesSamples[keyName].owned = 0;
      });

      //Delete users
      newState.users = {};
      pushToServer({});
      pushToServer(newState);
      return newState;
      break;
    case "change_settings":
      newState.settings.max_players = action.data.maxPlayers;
      newState.settings.min_players = action.data.minPlayers;
      newState.settings.timer_time = action.data.timerValue;
      newState.settings.random_shuffle_settings.def = action.data.def;
      newState.settings.random_shuffle_settings.cen = action.data.cen;
      newState.settings.random_shuffle_settings.atk = action.data.atk;
      newState.settings.game_type = action.data.gameType;
      newState.settings.timer = action.data.timer;
      newState.settings.random_shuffle = action.data.randomShuffle;
      pushToServer(newState);
      return newState;
      break;
    case "purchase_player":
      let index;
      Object.keys(newState.players).map(function (keyName, keyIndex) {
        if (newState.players[keyName].id === action.playerId) {
          index = keyName;
          return;
        }
      });
      if (index) {
        newState.players[index].purchased_by = action.user;
        newState.players[index].purchased_for = action.mln;
      } else {
        // @todo handle error
        return newState;
        break;
      }
      pushToServer(newState);
      return newState;
      break;
    case "update_player_user":
      let updatePlayerUserIndex;
      Object.keys(newState.players).map(function (keyName, keyIndex) {
        if (newState.players[keyName].id === action.playerId) {
          updatePlayerUserIndex = keyName;
          return;
        }
      });

      if (updatePlayerUserIndex) {
        newState.players[updatePlayerUserIndex].purchased_for = parseInt(
          action.newPrice
        );
        newState.players[updatePlayerUserIndex].purchased_by = action.newOwner;
      } else {
        // @todo handle error
        return newState;
        break;
      }
      clearTimeout(timer);
      timer = setTimeout(() => {
        pushToServer(newState);
      }, 3000);
      return newState;
      break;
    case "shuffle_update":
      let updatePlayerShuffleTimesIndex;
      Object.keys(newState.players).map(function (keyName, keyIndex) {
        if (newState.players[keyName].id === action.playerId) {
          updatePlayerShuffleTimesIndex = keyName;
          return;
        }
      });
      if (updatePlayerShuffleTimesIndex) {
        newState.players[updatePlayerShuffleTimesIndex].shuffled_times =
          parseInt(newState.settings.current_shuffle);
        newState.settings.current_shuffle =
          parseInt(newState.settings.current_shuffle) + 1;
      } else {
        // @todo handle error
        return newState;
        break;
      }
      pushToServer(newState);
      return newState;
      break;
    case "set_player_free":
      Object.keys(newState.players).map(function (keyName, keyIndex) {
        if (newState.players[keyName].id === action.id) {
          newState.players[keyName].purchased_for = 0;
          newState.players[keyName].purchased_by = "";
          newState.players[keyName].shuffled_times = "";
        }
      });
      pushToServer(newState);
      return newState;
      break;
    default:
      return state;
      break;
  }
};

const settings = createStore(settingsReducer);

export default settings;
