  const settings = useSelector((state) => (state ? state : null));

  // Get logged user
  const fantauser = JSON.parse(window.localStorage.getItem("fantauser"));

  // Set download JSON
  const downloadSettings = () => {
    var dataStr =
      "data:text/json;charset=utf-8," +
      encodeURIComponent(JSON.stringify(settings));
    var dlAnchorElem = document.getElementById("downloadAnchorElem");
    dlAnchorElem.setAttribute("href", dataStr);
    dlAnchorElem.setAttribute("download", "currentSettings.json");
    dlAnchorElem.click();
  };

