// Import react dependecies
import React from "react";
import { useState, useRef } from "react";
import { useSelector} from "react-redux";

// Import classes
import classes from "./Login.module.css";

const Login = (props) => {
  const [errorString, setErrorString] = useState(false);
  const username = useRef();
  const password = useRef();

  // Get the settings from the store
  const settings = useSelector((state) => state.settings);

  // Handle login
  const login = (e) => {
    e.preventDefault();
    const usernameValue = username.current.value;
    const passwordValue = password.current.value;
    let role;
    if (
      usernameValue !== settings.usernames[0] &&
      usernameValue !== settings.usernames[1]
    ) {
      setErrorString("Username non valido.");
      return;
    } else if (passwordValue !== settings.password) {
      setErrorString("Password non valida.");
      return;
    } else {
      if (usernameValue === settings.usernames[0]) {
        role = "user";
      } else if (usernameValue === settings.usernames[1]) {
        role = "admin";
      }
    }

    props.makeLogin(role);
  };

  // Restore error
  const onInputChange = () => {
    setErrorString();
  };

  return (
    <div className={classes.loginWrapper}>
      <form className={classes.login} onSubmit={login}>
        <h1>Login</h1>
        <input
          type="text"
          placeholder="Username"
          ref={username}
          onChange={onInputChange}
        />
        <input
          type="password"
          placeholder="Password"
          ref={password}
          onChange={onInputChange}
        />
        {errorString && <p className="error">{errorString}</p>}
        <button type='submit' className="primaryButton">
          Login
        </button>
      </form>
    </div>
  );
};

export default Login;
