import { Fragment } from "react";
import ReactDOM from "react-dom";

// Import modules
import AddUsers from "../Layout/AddUsers";
import GeneralSettings from "../Layout/GeneralSettings";
import Player from "../Layout/Player";
import UserDetail from "../Layout/UserDetail";
import PersonalArea from "../Layout/PersonalArea";

// import css classes
import classes from "./Modal.module.css";



const Backdrop = (props) => {
  return <div className={classes.backdrop} onClick={props.onClose} />;
};

const ModalOverlay = (props) => {
  return (
    <div className={classes.modal}>
      <div className={classes.content}>{props.children}</div>
    </div>
  );
};

const portalElement = document.getElementById("overlays");

const Modal = (props) => {
  // Create dynamically the content
  let content = "";
  if (props.content.action === "addUsers") {
    content = <AddUsers></AddUsers>;
  } else if (props.content.action === "generalSettings") {
    content = <GeneralSettings></GeneralSettings>;
  } else if (props.content.action === "selectPlayer") {
    content = <Player playerId={props.content.id} onShuffle={props.onSelectOption}></Player>;
  } else if (props.content.action === "userDetail") {
    content = <UserDetail userKey={props.content.userKey}></UserDetail>;
  }else if (props.content.action === "personalArea") {
    content = <PersonalArea ></PersonalArea>;
  }

  return (
    <Fragment>
      {ReactDOM.createPortal(
        <Backdrop onClose={props.onClose} />,
        portalElement
      )}
      {ReactDOM.createPortal(
        <>
          <ModalOverlay>
            <div className={classes.closeModal}>
              <svg
                height="20"
                viewBox="0 0 365.696 365.696"
                width="20"
                onClick={() => props.onClose({ isShown: false })}
              >
                <path d="m243.1875 182.859375 113.132812-113.132813c12.5-12.5 12.5-32.765624 0-45.246093l-15.082031-15.082031c-12.503906-12.503907-32.769531-12.503907-45.25 0l-113.128906 113.128906-113.132813-113.152344c-12.5-12.5-32.765624-12.5-45.246093 0l-15.105469 15.082031c-12.5 12.503907-12.5 32.769531 0 45.25l113.152344 113.152344-113.128906 113.128906c-12.503907 12.503907-12.503907 32.769531 0 45.25l15.082031 15.082031c12.5 12.5 32.765625 12.5 45.246093 0l113.132813-113.132812 113.128906 113.132812c12.503907 12.5 32.769531 12.5 45.25 0l15.082031-15.082031c12.5-12.503906 12.5-32.769531 0-45.25zm0 0" />
              </svg>
            </div>
            {content}
          </ModalOverlay>
          <div className={classes.space}></div>
        </>,
        portalElement
      )}
    </Fragment>
  );
};

export default Modal;
